import React from "react";
import lisa from "./images/lisa.png";

const HomePage = () => {
  return (
    <section className="home">
      <div className="home__center-sect">
        <img className="home__img" src={lisa} alt="" />
        <h1 className="home__title">Hello!</h1>
        <p className="home__descr">
          I'm Zubtsov Kirill, <br /> Junior Front-End Developer
        </p>
      </div>
    </section>
  );
};

export default HomePage;
