import React from "react";
import { Link } from "react-router-dom";
const Header = ({ burger, setBurger }) => {
  const toggleBurger = () => {
    setBurger(!burger);
  };
  return (
    <header className={`header ${burger ? "active" : ""}`}>
      <div
        className={`burger ${burger ? "active" : ""}`}
        onClick={toggleBurger}
      >
        <span className="burger__line"></span>
      </div>
      <nav class={`nav ${burger ? "active" : ""}`}>
        <ul class="nav__menu">
          <li class="nav__item">
            <Link to="/portfolio/" className="nav__link" onClick={toggleBurger}>
              Home
            </Link>
          </li>
          <li className="nav__item">
            <Link to="/portfolio/about" className="nav__link" onClick={toggleBurger}>
              About
            </Link>
          </li>
          <li className="nav__item">
            <Link className="nav__link" to="/portfolio/contact" onClick={toggleBurger}>
              Contact
            </Link>
          </li>
          <li className="nav__item">
            <Link to="/portfolio/project" className="nav__link" onClick={toggleBurger}>
              Project
            </Link>
          </li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;
