import React from "react";
import Inst from "./images/inst.png";
import Face from "./images/facebook.png";
import Twit from "./images/twitter.png";
import Whatsapp from "./images/whatsapp.png";
const Contact = () => {
  return (
    <section className="contact">
      <div className="home__center-sect">
        <div className="contact__col">
          <div className="contact__row">
            <div className="contact__text">
              <p className="contact__number">
                My number: <br /> +996553121929
              </p>
              <p className="contact__email">
                My Email: <br /> zubcov3986@gmail.com
              </p>
            </div>
          </div>
          <div className="contact__row">
            <div className="contact__form">
              <input className="contact__inp" type="text" placeholder="Name" />
              <input
                className="contact__inp"
                type="email"
                placeholder="Email"
              />
              <input
                className="contact__bigInp"
                type="text"
                placeholder="Message"
              />
            </div>
            <button className="contact__btn">Send Message</button>
          </div>
          <div className="contact__row">
            <div className="contact__web">
              <img src={Inst} alt="" className="contact__img-inst" />

              <img src={Face} alt="" className="contact__img-face" />

              <img src={Twit} alt="" className="contact__img-twit" />

              <img src={Whatsapp} alt="" className="contact__img-what" />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
};

export default Contact;
