import React from "react";
import WD from "./images/wh.jpeg";
import Fura from "./images/fura.jpeg";
import Todo from "./images/todo.jpeg";
import Weather from "./images/Weather.jpeg";
const Project = () => {
  return (
    <section className="project">

      <div className="home__center-sect">
      <div className="project__transform">
        <h2 className="project__title">My Works</h2>
        <div className="project__boxcont">
          <div className="project__box">
            <img className="project__box__img" src={WD} alt="" />
            <a href="https://zubcov01.github.io/Kirill/">
              <button className="project__box__btn">Open Project</button>
            </a>
          </div>
          <div className="project__box">
            <img className="project__box__img" src={Fura} alt="" />
            <a href="https://zubcov01.github.io/truck/">
              <button className="project__box__btn">Open Project</button>
            </a>
          </div>
          <div className="project__box">
            <img className="project__box__img" src={Todo} alt="" />
            <a href="https://zubcov01.github.io/ToDo-App/">
              <button className="project__box__btn">Open Project</button>
            </a>
          </div>
          <div className="project__box">
            <img className="project__box__img" src={Weather} alt="" />
            <a href="https://zubcov01.github.io/weather-app/">
              <button className="project__box__btn">Open Project</button>
            </a>
          </div>
        </div>
      </div>
      </div>
    </section>
  );
};

export default Project;
